.about {
  padding-top: 80px;
  padding-bottom: 40px;
  background-color: var(--fon-color);

  &__title {
    margin: 0;
    max-width: 845px;
    font-size: 48px;
    font-weight: 500;
    line-height: 61px;
    color: var(--light-color);
    margin-bottom: 40px;
    &-red {
      color: var(--accent-color);
    }
  }
  &__text {
    max-width: 650px;
    margin: 0;
    color: rgb(214, 214, 214);
    font-size: 20px;
    font-weight: 400;
    line-height: 26px;
    margin-bottom: 50px;
  }

  &__list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    &::after {
      content: "";
      position: absolute;
      /* Ellipse 264 */
      position: absolute;
      width: 207.01px;
      height: 277px;
      top: 50px;
      right: 0;
      transform: rotate(25.66deg);
      filter: blur(300px);
      background: rgb(255, 0, 0);
    }
    &::before {
      content: "";
      position: absolute;
      /* Ellipse 264 */
      position: absolute;
      width: 207.01px;
      height: 277px;
      top: 100px;
      left: 100px;
      transform: rotate(25.66deg);
      filter: blur(170px);
      background: rgb(19, 136, 215);
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: space-between;
    padding: 25px;

    &:nth-child(odd) {
      box-sizing: border-box;
      border-radius: 22px;

      backdrop-filter: blur(8px);
      background: linear-gradient(
        30.47deg,
        rgba(6, 102, 159, 0.6) 12.431%,
        rgba(2, 145, 213, 0.6) 77.247%
      );
    }

    &:nth-child(even) {
      box-sizing: border-box;
      border-radius: 22px;

      backdrop-filter: blur(8px);
      background: rgba(21, 10, 10, 0.6);
    }

    &--text {
      color: rgb(255, 255, 255);
      font-size: 20px;
      font-weight: 500;
      line-height: 26px;
    }
  }
}
