.advantages {
  padding-top: 80px;
  padding-bottom: 40px;
  background-color: var(--fon-color);

  &_container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-size: 48px;
    font-weight: 400;
    line-height: 61px;
    letter-spacing: 0%;
    text-align: center;
    color: var(--light-color);

    &-red {
      color: var(--accent-color);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;
    padding: 50px 30px;
    box-sizing: border-box;
    border-radius: 22px;
    backdrop-filter: blur(8px);
    background: rgba(21, 10, 10, 0.6);
    margin-bottom: 45px;
  }

  &__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;

    &-logo {
      border-radius: 50px;
      width: 100px;
      height: 100px;
      background-color: var(--light-color);
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 28px;
    }

    &-info {
      &--title {
        color: var(--light-color);
        font-size: 32px;
        font-weight: 400;
        line-height: 41px;
        letter-spacing: 0%;
      }

      &--text {
        color: var(--light-color);
        font-size: 20px;
        font-weight: 400;
        line-height: 26px;
        letter-spacing: 0%;
      }
    }
  }
}
