.hero {
  padding-top: 160px;

  background-color: var(--fon-color);

  &__container {
    @include flex-v-center;
  }

  &__content {
    max-width: 633px;

    @include high-tablet {
      max-width: none;
    }
  }

  &__block {
    position: relative;

    display: flex;
    align-items: center;
    padding: 40px;
    // border: 1px solid;
    // border-image-source: linear-gradient(
    //   111.95deg,
    //   rgba(181, 181, 181, 0.49) 40.78%,
    //   rgba(0, 0, 0, 0) 62.31%,
    //   rgba(191, 191, 191, 0.76) 62.31%
    // );
    box-sizing: border-box;
    border-radius: 22px;

    backdrop-filter: blur(8px);
    background: rgba(21, 10, 10, 0.6);

    &::after {
      content: "";
      /* Ellipse 248 */
      content: "";
      position: absolute;
      width: 202px;
      height: 170px;
      top: -85px;
      left: 0;
      // background-image: url("../img/Ellipse-red.png");
      // background-size: cover;
      filter: blur(129px);
      background: rgb(255, 0, 0);
    }
    &::before {
      content: "";
      /* Ellipse 248 */
      content: "";
      position: absolute;
      width: 202px;
      height: 170px;
      top: 50px;
      right: 40px;
      // background-image: url("../img/Ellipse-red.png");
      // background-size: cover;
      filter: blur(100px);
      background: rgb(19, 136, 215);
    }
  }

  &__image {
    transform: translateX(10px);

    @include high-tablet {
      display: none;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 50px;
    font-weight: 400;
    font-size: 45px;
    line-height: 61px;

    color: var(--light-color);

    &--red {
      color: var(--accent-color);
    }

    &-text {
      display: block;
      max-width: 90%;
      font-weight: 400;
      font-size: 20px;
      line-height: 160%;
      text-transform: initial;
    }
  }

  @include high-tablet {
    padding-bottom: 100px;
    min-height: auto;
  }
}
