.modal {
  &__form-input {
    border: 1px solid var(--dark-color);
    border-radius: 10px;
  }

  &__title {
    margin: 0;
    font-weight: 500;
    font-size: 28px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 30px;
  }

  &__subtitle {
    margin: 0;
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);
    margin-top: 15px;
    margin-bottom: 10px;

    &-italic {
      font-family: "Roboto-Italic";
    }
  }

  &__text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);

    &-italic {
      font-family: "Roboto-Italic";
    }
  }

  &__info-text {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);
    margin-bottom: 30px;
  }

  &__table {
    &--first {
      display: grid;
      grid-template-columns: repeat(12, 1fr);
      gap: 10px;
      border: 2px solid #f3f3f5;
      border-radius: 5px;
      padding: 5px;

      @include small-tablet {
        grid-template-columns: repeat(10, 1fr);
      }
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
    &--left-block {
      grid-column: 4 span;
      border-right: 2px solid #f3f3f5;
      @include small-tablet {
        grid-column: 10 span;
        border-right: none;
      }
    }
    &--right-block {
      grid-column: 8 span;
      @include small-tablet {
        grid-column: 10 span;
      }
    }
  }
}
