.form {
  display: flex;
  flex-direction: column;
  width: 100%;

  &__label {
    display: flex;
    flex-direction: column-reverse;
    margin-bottom: 15px;
    width: 100%;
  }

  &__input {
    border-radius: 22px;
    width: 100%;
    height: 64px;
    padding: 0 20px;
    font-weight: 400;
    font-size: 18px;
    line-height: 26px;
    color: var(--dark-color);

    &::placeholder {
      color: rgba(40, 41, 68, 0.7);
      @include mobile {
        font-size: 14px;
      }
    }
  }

  &__btn {
    border-radius: 22px;
  }
}

.just-validate-error-label {
  margin-bottom: 3px;
  color: var(--accent-color);
}
