.tarifs {
  padding-top: 80px;
  padding-bottom: 40px;
  background-color: var(--fon-color);

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-size: 48px;
    font-weight: 400;
    line-height: 61px;
    color: var(--light-color);
    text-align: center;

    &-red {
      color: var(--accent-color);
    }
  }

  &__list {
    position: relative;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;

    &::after {
      content: "";
      position: absolute;
      /* Ellipse 264 */
      position: absolute;
      width: 207.01px;
      height: 277px;
      top: 0px;
      right: 50px;
      transform: rotate(25.66deg);
      filter: blur(250px);
      background: rgb(19, 136, 215);
    }

    &::before {
      content: "";
      position: absolute;
      /* Ellipse 264 */
      position: absolute;
      width: 207.01px;
      height: 277px;
      top: 100px;
      left: 100px;
      transform: rotate(25.66deg);
      filter: blur(170px);
      background: rgb(19, 136, 215);
    }
  }

  &__item {
    position: relative;
    padding: 25px;
    color: var(--light-color);
    font-size: 24px;
    font-weight: 400;
    line-height: 31px;

    &-icon {
      position: absolute;
      top: 0;
      right: -15px;
      @include mobile {
        right: -11px;
        top: -36px;
      }
      &--text {
        position: absolute;
        font-weight: 400;
        font-size: 20px;
        color: var(--light-color);
        transform: rotate(-15deg);
        right: 4px;
        @include mobile {
          top: -14px;
        }
      }
    }

    &:last-child {
      z-index: 10;
      position: relative;
      box-sizing: border-box;
      border-radius: 22px;
      backdrop-filter: blur(8px);
      background: rgb(227, 70, 70);
      display: inline-flex;
      align-items: center;
      justify-content: center;
      transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

      &:hover {
        background-color: var(--blue-color);
      }

      & > a::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  &__item:not(:last-child) {
    box-sizing: border-box;
    border-radius: 22px;
    backdrop-filter: blur(8px);
    background: rgba(0, 0, 0, 0.6);
  }
}
