.steps {
  padding-top: 80px;
  padding-bottom: 40px;
  background-color: var(--fon-color);

  &__title {
    margin: 0;
    margin-bottom: 45px;
    font-size: 48px;
    font-weight: 400;
    line-height: 61px;
    color: var(--light-color);

    &-red {
      color: var(--accent-color);
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      /* Ellipse 264 */
      position: absolute;
      width: 207.01px;
      height: 277px;
      top: -50px;
      right: 50px;
      transform: rotate(25.66deg);
      filter: blur(250px);
      background: rgb(19, 136, 215);
    }
    &::before {
      content: "";
      position: absolute;
      /* Ellipse 264 */
      position: absolute;
      width: 207.01px;
      height: 277px;
      top: 100px;
      left: 100px;
      transform: rotate(25.66deg);
      filter: blur(170px);
      background: rgb(255, 0, 0);
    }
  }

  &__item {
    padding: 15px 30px;
    color: var(--light-color);
    font-size: 24px;
    font-weight: 400;
    line-height: 31px;
    display: flex;
    align-items: center;

    &--blue {
      box-sizing: border-box;
      border-radius: 22px;
      backdrop-filter: blur(8px);
      background: linear-gradient(
        9.54deg,
        rgba(6, 102, 159, 0.6) 22.449%,
        rgba(2, 145, 213, 0.6) 58.488%
      );
    }

    &--dark {
      box-sizing: border-box;
      border-radius: 22px;
      backdrop-filter: blur(8px);
      background: rgba(21, 10, 10, 0.6);
    }

    &--red {
      z-index: 10;
      position: relative;
      box-sizing: border-box;
      border-radius: 22px;
      backdrop-filter: blur(8px);
      background: rgb(227, 70, 70);
      display: inline-flex;
      align-items: center;
      justify-content: center;
      transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;

      &:hover {
        background-color: var(--blue-color);
      }

      & > a::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }

  &_link {
    // @include flex-all-center;
    color: var(--light-color);
  }
}
