.contacts {
  padding-top: 80px;
  padding-bottom: 40px;
  background-color: var(--fon-color);

  &__title {
    margin: 0;
    margin-bottom: 40px;
    color: var(--light-color);
    font-size: 48px;
    font-weight: 400;

    & > span {
      color: var(--accent-color);
    }
  }

  &__block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px;

    &-left {
      padding: 30px;
      box-sizing: border-box;
      border-radius: 22px;
      backdrop-filter: blur(8px);
      background: linear-gradient(
        9.54deg,
        rgba(6, 102, 159, 0.6) -17.405%,
        rgba(2, 145, 213, 0.6) 86.887%
      );
    }

    &-right {
      position: relative;
      padding: 30px;
      box-sizing: border-box;
      border-radius: 22px;
      backdrop-filter: blur(8px);
      background: rgba(21, 10, 10, 0.6);

      &::after {
        content: "";
        position: absolute;
        /* Ellipse 264 */
        position: absolute;
        width: 166.01px;
        height: 235px;
        top: 150px;
        left: 233px;
        transform: rotate(25.66deg);
        filter: blur(180px);
        background: rgb(255, 0, 0);
      }

      &--img {
        position: absolute;
        right: 30px;
        border-radius: 50px;
        width: 40px;
        height: 40px;
        background-color: #065179;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }

  &__subtitle {
    margin: 0;
    margin-bottom: 50px;
    color: var(--light-color);
    font-size: 24px;
    font-weight: 400;
  }

  &__tel {
    color: var(--light-color);
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 40px;
    &-info {
      color: var(--light-color);
      font-size: 24px;
      font-weight: 400;

      margin-bottom: 7px;
    }
  }

  &__text {
    margin: 0;
    color: var(--light-color);
    font-size: 24px;
    font-weight: 400;
  }
}
