.footer {
  padding-top: 40px;
  padding-bottom: 40px;

  background: #095780;

  &__block {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
    margin-bottom: 30px;
  }

  &__text {
    margin: 0;
    margin-bottom: 7px;
    color: var(--light-color);

    font-size: 20px;
    font-weight: 400;
  }

  &-right {
    display: flex;
    justify-content: center;
  }

  &__copyright {
    text-align: right;
    color: var(--light-color);
    font-size: 20px;
    font-weight: 400;
  }
}
