.header {
  position: absolute;
  left: 0;
  top: 0;
  padding: 22px 0;
  width: 100%;
  z-index: 10000;

  &__container {
    @include flex-all-sb;

    @include tablet {
      justify-content: initial;
    }
  }

  &__phone {
    position: relative;
    border: 2px solid rgb(227, 70, 70);
    border-radius: 12px;
    padding: 13px 14px 14px 45px;
    color: rgb(227, 70, 70);
    font-family: Onest;
    font-size: 16px;
    font-weight: 500;
    &::before {
      content: "";
      position: absolute;
      width: 17px;
      height: 17px;
      left: 10px;
      background-image: url("../img/phone.png");
      background-size: cover;
    }

    // @include tablet {
    //   position: relative;
    //   margin-right: 10px;
    //   margin-left: auto;
    //   border: 1px solid rgba(255, 255, 255, 0.08);
    //   border-radius: 10px;
    //   width: 44px;
    //   height: 44px;
    //   font-size: 0;
    //   background-image: url("../img/phone.png");
    //   background-position: center;
    //   // background-size: 24px 24px;
    //   background-repeat: no-repeat;
    // }
  }
  &--fixed {
    position: fixed;
    background-color: #1d1a1b;
    left: 0;
    top: 0;
    z-index: 10000;
  }
}
